:root {
  font-family: "Avenir", Arial, sans-serif;
  line-height: 1.6;
}

:root * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  overflow: hidden;
  font-family: "Avenir", Arial, sans-serif;
}

.wrapper .icon {
  fill: #888888;
  transition: all .2s ease;
}

.main {
  position: relative;
}

.main:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  -webkit-box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
  box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
  z-index: 10;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 1%;
  padding-right: 1%;
  box-sizing: content-box;
}

a {
  transition: all 0.3s ease;
  color: inherit;
}

.add-plus {
  position: absolute;
  top: 44%;
  right: -46px;
  z-index: 10;
  font-size: 65px;
  width: 1em;
  height: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #fff;
  background-color: #BBBBBB;
  border-radius: 50%;
  -webkit-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.36);
  -moz-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.36);
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.36);
}

@media (max-width: 1200px) {
  .add-plus {
    font-size: calc(24.2px + 3.4vw) ;
  }
}

.add-plus .icon {
  font-size: 36.41px;
  fill: #fff;
}

@media (max-width: 1200px) {
  .add-plus .icon {
    font-size: calc(18.482px + 1.494vw) ;
  }
}

.plan__icons-box {
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.plan__icon {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #B27541;
  border-radius: 50%;
}

.icon-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  padding-left: 1px;
  border: 1.5px solid #F0CDB0;
  border-radius: 50%;
}

.icon-wrap .icon-sign-in {
  font-size: 18px;
  fill: #F0CDB0;
}

.header {
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px 10% 20px 3.5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 116px;
}

.header .header__logo {
  width: 107px;
}

.header .header__logo img {
  width: 100%;
}

.header .header__nav {
  display: flex;
  flex-grow: 1;
